*{
    margin: 0;
    box-sizing: border-box;
}
::-webkit-scrollbar{
    background: #343a40;
    width: 5px;
}
::-webkit-scrollbar-thumb{
    
    background: #6c757d;
}
#root{
    background: #212529;
    min-height: 100vh;
    display: grid;
    place-items: center;
}
.main-div{
    width: 30%;
    min-height: 80vh;
}
.img{
    height: 5rem;
    
}
.img img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.height{
    max-height: 15rem;
}
.list{
    background-color: #8b00f7;
    color: #E0E0E0;
}
.list:hover{
    background-color: #E0E0E0;
    color: red;
}
.text{
    font-weight: 500;
}
.efff{
    background: #E0E0E0 !important;
}
@media only screen and (max-width: 600px){
    .main-div{
        height: 100%;
        width: 100%;
    }
}